<template lang="html">
  <div class="keywords-list">
    <div class="keywords-list-item clearfix" :class="{borderB: borderB }">
      <!-- 左侧 -->
      <div class="keywords-list-item__left fl">{{tag}}</div>
      <!-- 中间 -->
      <div class="keywords-list-item__center fl">
        <input @input="changeKeyword" :disabled="disabled" class="fl" type="text" name="" v-model="curtext" :placeholder="texthold" :style="{'width': leftWidth}">
        <input @input="changeNumber" class="fl" type="number" name="" v-model="curnumber" disabled="true" :placeholder="numberhold">
      </div>
      <i class="rmb-icon fl">¥</i>
      <!-- 右侧 -->
      <div class="keywords-list-item__right fl" @click="popMessage(status)" :class="{ 'activeColor': isPass, 'wrongColor': !isPass }">{{status}}</div>
      <i class="sign-icon fr wrongColor" v-show="(leftWidth === '2.3rem')">*</i>
    </div>

  </div>
</template>

<script>
import api from '@/api'
import _ from 'underscore'
import minutils from '@/utils/minUtils'
export default {
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    index: {
      type: Number
    },
    keyword: {
      type: String,
      default: ''
    },
    number: {
      type: Number
    },
    tag: {
      type: String
    },
    texthold: {
      type: String
    },
    numberhold: {
      type: Number | String,
      default: 0.00
    },
    status: {
      type: String
    },
    isPass: {
      type: Boolean
    },
    borderB: {
      type: Boolean
    },
    leftWidth: {
      type: String,
      default: '2.3rem'
    }
  },
  watch: {
    keyword (val) {
      this.curtext = val
    },
    number (val) {
      this.curnumber = val
    }
  },
  data () {
    return {
      curtext: this.keyword,
      curnumber: this.number
    }
  },
  methods: {
    popMessage (status) {
      if (status === '待审核') {
        minutils.Toast({message: '该标签关键词涉及违反相关法律或涉及敏感内容！请修改关键词！', position: 'top'})
      }
    },
    changeKeyword: _.throttle(function (e) {
      let len = this.curtext.length
      if (len > 10) {
        this.curtext = this.curtext.substring(0, 10)
        minutils.Toast('请输入标签关键词10字以内')
        return
      }
      this.$emit('submit-keynumber', this.index, this.curtext, this.curnumber)
      // this.ajaxCheckWord(e)
    }, 800),
    ajaxCheckWord (e) {
      api.isSenitiveword({name: e.target.value}).then(response => {
        let result = response.body
        if (result.isSuccess) {
          let data = result.data
          if (data.type === 1) this.messageBox(e)
          else if (data.type === 2) this.messageBoxRules(e)
          else this.$emit('submit-keynumber', this.index, this.curtext, this.curnumber)
        }
      })
    },
    messageBox (e) {
      let self = this
      minutils.MessageBoxConfirm({
        message: '该标签关键词涉及高危, 需要上传"行业资质", 请修改关键词或上传资质?',
        options: { cancelButtonText: '返回', confirmButtonText: '上传' },
        success () {
          self.$router.push('/personrecognize')
        },
        fail () {
          e.target.value = ''
        }
      })
    },
    messageBoxRules (e) {
      // let self = this
      minutils.MessageBoxConfirm({
        message: '该标签关键词涉及违反相关法律或涉及敏感内容！请修改关键词！',
        options: { cancelButtonText: '违规说明', confirmButtonText: '确定' },
        success () {
          // self.$router.push('/personrecognize')
        },
        fail () {
          e.target.value = ''
        }
      })
    },
    changeNumber: _.throttle(function (e) {
      let val = e.target.value
      if (val < 0 || val > 10) {
        e.target.value = ''
        minutils.Toast('请输入0 - 10之间的数字')
      } else {
        this.$emit('submit-keynumber', this.index, this.curtext, this.curnumber)
      }
    }, 500)
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.keywords-list {
  .keywords-list-item {
    width: 100%;
    height: 1.28rem;
    line-height: 1.28rem;
    font-size: .32rem;

    .keywords-list-item__left {
      width: 1.15rem;
    }
    .keywords-list-item__center {
      // width: 4.1rem;
      // height: .88rem;
      margin-top: .18rem;
      border: .01rem solid @lightColor;
      input {
        height: .88rem;
        border: 0;
        font-size: .28rem;
        color: @lightColor;
      }
      input:first-child {
        // width: 2.3rem;
        padding-left: .1rem;
        border-radius: 0;
        border-right: .01rem solid @lightColor;
      }
      input:last-child {
        width: 1.75rem;
        font-size: .3rem;
        padding-left: .4rem;
        color: @defaultColor;
        &::placeholder {
          color: @defaultColor;
        }
      }
    }
    .rmb-icon {
      margin-left: -1.6rem;
    }
    .keywords-list-item__right {
      margin-left: .2rem;
      width: 1.4rem;
    }
    .sign-icon {
      font-size: .36rem;
      position: relative;
      top: .1rem;
    }
  }
}
</style>



