<template lang="html">
  <div class="keywords-page">
    <!-- 头部 -->
    <default-head header="个人标签关键词">
      <!-- <a href="#" slot="head-right">批量调价</a> -->
    </default-head>
    <div class="key-word-box">
      <div class="keywords-data" v-for="index in 10" >
        <keywords-list v-if="wordsData[index-1]"
         :isPass="wordsData[index-1].status === 1"
         :tag="'标签' + index"
         :index = "index - 1"
         :keyword="wordsData[index-1].name"
         texthold="请输入标签关键词(10字以内)"
         :number="wordsData[index-1].price"
         :status="status[wordsData[index-1].status]"
         :disabled="wordsData[index-1].status === 1"
         @submit-keynumber = "getKeyNumber"
         ></keywords-list>
        <keywords-list v-else leftWidth="3.77rem" :index = "index - 1" @submit-keynumber="getKeyNumber" :tag="'标签' + index"
        texthold="请输入标签关键词(10字以内)" status="" ></keywords-list>
      </div>
    </div>
    <!-- 保存按钮 -->
    <foot-btn @click.native="submitKeyWord" text="保存"></foot-btn>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import keywordsList from '@/components/keywords/keywordsList'
import footBtn from '@/components/common/footBtn'

export default {
  data () {
    return {
      wordsData: [],
      status: ['待审核', '通过', '审核拒绝']
    }
  },
  components: { defaultHead, keywordsList, footBtn },
  created () {
    this.initPage()
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      this.ajaxGetUserTagsKeyWord({userId: this.userId})
    },
    getKeyNumber (index, key, price) {
      this.wordsData[index] = {...this.wordsData[index], name: key, userId: this.userId, price: price, change: 1, status: 0}
    },
    submitKeyWord () {
      this.$nextTick(() => {
        let data = this.wordsData.filter(item => {
          item.userId = this.userId
          item.price = this.price || 0
          return (item.status === 0 || item.status === 2) && item.name
        })
        // 验证数据
        if (data.length <= 0 && this.wordsData.length > 0) {
          minutils.MessageBox({message: '您的关键词不完整或已全部通过, 请不要重复提交'})
          return
        } else if (data.length <= 0) {
          minutils.MessageBox({message: '你还未填写关键词,或所填关键词信息不全,请勿提交'})
          return
        }
        this.ajaxSaveUserKeyWord({tags: data})
      })
    },
    ajaxSaveUserKeyWord (data) {
      minutils.IndicatorOpen('上传中...')
      api.saveUserKeyWord(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          let self = this
          minutils.MessageBox({
            message: '保存个人标签关键词成功',
            callback () {
              self.wordsData = []
              self.initPage()
            }
          })
        }
      })
    },
    ajaxGetUserTagsKeyWord (data) {
      minutils.IndicatorOpen('加载中...')
      api.getUserTagsKeyWordByUserId(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data) {
          this.wordsData = this.wordsData.concat(result.data)
          if (this.wordsData.length < 10) {
            this.wordsData.concat(new Array((10 - result.data.length)))
          }
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.keywords-page {
  padding-top: .88rem;
  padding-bottom: 1.5rem;
  background: @whiteColor;
  .head-right a {
    color: @activeColor;
  }
  .keywords-data, .add {
    padding: 0 .22rem;
    background: @whiteColor;

  }

}
</style>



